<template>
  <el-row class="form-bg">
    <el-col :span="10" :offset="6">
      <el-form ref="form" :rules="rules" :model="forminfo" label-width="100px">
        <el-form-item label="属性名" prop="specsname">
          <el-input v-model="forminfo.specsname" placeholder="请输入属性名"></el-input>
        </el-form-item>
        <el-form-item :label="index==0 ?'属性值':''" v-for="(item,index) in attrValue" :key="index" >
           <el-input style="width:200px;" v-model="item.value"></el-input>&nbsp;&nbsp;&nbsp;
           <el-button type="primary" @click="addAttrs" v-if="index==0">添加属性</el-button>
           <el-button type="warning" @click="delAttrs(index)" v-else>删除属性</el-button>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-switch v-model="forminfo.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">立即{{$route.params.id ? '修改':'创建'}}</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import {  addSpecs,editSpecs,getSpecsInfo } from "@/request/specs";
export default {
  data() {
    return {
      forminfo: {
        specsname:"",
        attrs:"",
        status: 1, // 状态，1是正常，2是禁用
      },
      attrValue:[{value:''}],
      rules: {
        specsname: [{ required: true, message: "请输入属性名称", trigger: "blur" }],
      },
    };
  },
  created() {
    // 如果是修改页面，就请求当前页面的数据！
    if (this.$route.params.id) {
      this.getinfo();
    }
  },
  methods: {
    addAttrs(){
       this.attrValue.push({
         value:""
       })
    },
    delAttrs(idx){
      this.attrValue.splice(idx,1)
    },
    async getinfo() {
      let res  = await getSpecsInfo(this.$route.params.id);
      if(res){
        this.attrValue = res.attrs.map(val=>{
          return {
            value:val
          }
        })
        this.forminfo = res;
      }
    },
    submit() {
      // 验证
      let state = true;
      this.attrValue.forEach(val=>{
        if(!val.value){
          state = false;
        }
      })
      if(!state){
        this.$message.warning("请输入属性值");
        return;
      }
      this.forminfo.attrs = this.attrValue.map(val=>{
        return val.value
      })
      this.forminfo.attrs = this.forminfo.attrs.join(",")
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 提交数据
          if (this.$route.params.id) {
            editSpecs(this.$route.params.id, this.forminfo);
          } else {
            addSpecs(this.forminfo);
          }
        } else {
          this.$message.warning("验证失败");
        }
      });
    },
    reset() {
      if (this.$route.params.id) {
        this.getinfo();  // 修改页面的重置其实就是做一下页面第一次进入请求数据赋值的那个事情！
      } else {
        this.$refs.form.resetFields();
      }
    },
  },
  components: {},
};

// BootStrap
</script>
<style scoped>
</style>


